import { theme, extendTheme } from "@chakra-ui/react";
import memoizeOne from 'memoize-one';
import { createMedia } from "@artsy/fresnel"
import { isArray, isNil, get } from "lodash";

export const breakpoints = [0, 768, 992, 1200, 1920];
const chakraBpNames = ['sm', 'md', 'lg', 'xl']

const chakraBps = chakraBpNames.reduce((bps, name, i) => {
  bps[name] = `${breakpoints[i + 1]}px`
  return bps
}, {})

export const responsiveIndex = [
  [0, 'mobile'],
  [1, 'tablet'],
  [2, 'laptop'],
  [3, 'desktop'],
]

const mediaBreak = responsiveIndex.reduce((obj, [i, name]) => {
  obj[name] = breakpoints[i]
  return obj
}, {})

const AppMedia = createMedia({
  breakpoints: mediaBreak
})
export const mediaStyle = AppMedia.createMediaStyle()
export const { Media, MediaContextProvider } = AppMedia

const handleCalc = (syn, a) => isNil(a) ? null : [syn[0], a, syn[1]].join('')

export const responsiveCalc = memoizeOne((syn, resArr) => {
  return isArray(resArr) ? resArr.map(a => handleCalc(syn, a)) : handleCalc(syn, resArr)
})

const responsiveMap = breakpoints.map((_, i) => {
  const id = responsiveIndex.findIndex(([ri]) => ri + 1 > i)
  return id >= 0 ? id : responsiveIndex.length
})

export const responsive = memoizeOne((...args) => {
  const argsLen = args.length
  if (argsLen <= 1) return args[0]
  return breakpoints.map((_, i) => get(args, [responsiveMap[i]], null))
});

export const mobileOrDesktop = responsive

export const containerWidth = ["26em", "44em", "58em", "76em"];
export const containerPadding = responsive('1em', '2em')

const font = 'Arial, "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';
const fonts = {
  heading: font,
  body: font,
  mono: "Menlo, monospace",
}

const lightGray = '#E6E6E6';
const gray = '#979797';
const darkGray = '#707070';
const red = '#EA5204';
const prussianBlue = '#002A5E';
const darkBlue = '#002F7B';
const brightBlue='#0074B7'
const ceruleanBlue = '#13E6F0';
const darkViolet = '#7D3787';
const vividViolet = '#A446BA'
const gigas = '#4F3398'
const mustard = '#FFDC53'
const pinkSalmon = '#FF90A1'
const mandy = '#df536A'
const disco = '#B71962'
const titleBlue = '#63CFE3'
const bgGreen = '#00796D'
const noteGreen = '#008874'
const darkGreen = '#035560'
const lime = '#BAE659'
const green = '#76BA5A'
const lightRed = '#CB105E'
const yellow = '#F6AC19'
const purple = '#814F9C'

const overrides = {
  fonts,
  colors: {
    ...theme.colors,
    red,
    lightRed,
    yellow,
    purple,
    green,
    lightGray,
    darkGray,
    darkBlue,
    gray,
    darkViolet,
    bgGreen,
    darkGreen,
    prussianBlue,
    ceruleanBlue,
    brightBlue,
    mandy,
    gigas,
    titleBlue,
    noteGreen,
    blue: prussianBlue,
    milkBlue: brightBlue,
    neonBlue: ceruleanBlue,
    mustard,
    vividViolet,
    pinkSalmon,
    disco,
    lime,
    primary: prussianBlue,
    primaryHover: ceruleanBlue,
  },
  breakpoints: chakraBps,
  containerWidth,
}

const customTheme = extendTheme(overrides)

export default customTheme
